const About = (props) => {
  return(
    <div id='about'>
      <h1 className='h1-title'>{props.title}</h1>

      <article>
        {props.text1}
      </article>

      <div className='about-container'>
        <picture className='about-image'>
          <source media="(min-height: 100px)" srcSet={web} className="about-image"/>
          <img src={web} className="about-image"/>
        </picture>

        <div className='vertical-line'></div>

        <article className='about-article'>
          Java, JavaScript, TypeScript, C, C++, C#, Python, Haskell <br/>
          Kotlin, React Native, Android Studio <br/>
          HTML, CSS, React
        </article>
      </div>

      <div className='about-container-right'>
        <article className='about-article-right'>
          Adobe Photoshop, Adobe XD, Adobe InDesign <br/>
          Figma, Canva, Clip Studio Paint
        </article>

        <div className='vertical-line-right'></div>

        <picture className='about-image'>
          <source media="(min-height: 100px)" srcSet={vector} className="about-image"/>
          <img src={vector} className="about-image"/>
        </picture>
      </div>

      <div className='about-container'>
        <picture className='about-image'>
          <source media="(min-height: 100px)" srcSet={settings} className="about-image"/>
          <img src={settings} className="about-image"/>
        </picture>

        <div className='vertical-line'></div>

          <article className='about-article'>
            Arduino, Git <br/>
            Sony VEGAS Pro, Unity, Blender
          </article>
      </div>

      <div className='about-container-right'>
        <article className='about-article-right'>
          {props.text2} <br/>
          {props.text3} <br/>
          {props.text4}
        </article>

        <div className='vertical-line-right'></div>

        <picture className='about-image'>
          <source media="(min-height: 100px)" srcSet={drawing} className="about-image"/>
          <img src={drawing} className="about-image"/>
        </picture>
      </div>

      <div className='about-container'>
        <picture className='about-image'>
          <source media="(min-height: 100px)" srcSet={language_image} className="about-image"/>
          <img src={language_image} className="about-image"/>
        </picture>

        <div className='vertical-line'></div>

        <article className='about-article'>
          {props.text5} <br/>
          {props.text6} <br/>
          {props.text7} <br/>
          {props.text8} <br/>
        </article>
      </div>
    </div>
  )
}

const Contact = (props) => {
  return(
    <div id='contact'>
      <h1 className='h1-title'>{props.title}</h1>

      <article>
        {props.text}
      </article>

      <div className='contact-container'>
        <div className='contact-item'>
          <a href='https://github.com/LauraCoura'>
          <picture className='contact-image'>
            <source media="(min-height: 70px)" srcSet={github} className="contact-image"/>
            <img src={github} className="contact-image"/>
          </picture>
          <h3>GitHub</h3>
          <p>LauraCoura</p>
          </a>
        </div>

        <div className='contact-item'>
          <a href='https://www.linkedin.com/in/laura-coura/'>
          <picture className='contact-image'>
            <source media="(min-height: 70px)" srcSet={linkedin} className="contact-image"/>
            <img src={linkedin} className="contact-image"/>
          </picture>
          <h3>LinkedIn</h3>
          <p>laura-coura</p>
          </a>
        </div>
      </div>
    </div>
  )
}

const ContactMobile = (props) => {
  return(
    <div id='contact'>
      <h1 className='h1-title'>{props.title}</h1>

      <article>
        {props.text}
      </article>

      <div className='contact-container-mobile'>
        <div className='contact-item-mobile'>
          <picture className='contact-image'>
            <source media="(min-height: 70px)" srcSet={github} className="contact-image"/>
            <img src={github} className="contact-image"/>
          </picture>
          <h3>GitHub</h3>
          <p>LauraCoura</p>
        </div>

        <div className='contact-item-mobile'>
          <picture className='contact-image'>
            <source media="(min-height: 70px)" srcSet={linkedin} className="contact-image"/>
            <img src={linkedin} className="contact-image"/>
          </picture>
          <h3>LinkedIn</h3>
          <p>laura-coura</p>
        </div>
      </div>
    </div>
  )
}

const Home = () => {
    const [language, setLanguage] = useState(0); // 0 is english, 1 is portuguese
    const [displayMenu, setDisplayMenu] = useState(false);

    const [width, setWidth] = useState();

    const resizeHandler = () => {
      setWidth(window.innerWidth);
    };

    useEffect(() => {
      window.addEventListener('resize', resizeHandler);

      // Cleanup function
      // Remove the event listener when the component is unmounted
      return () => {
        window.removeEventListener('resize', resizeHandler);
      }
    }, []);

    const Header = (props) => {
      return(
        <header className="header">
          <div className="header-left">
            <a href='/'>{props.home}</a>
            <a href='/#about'>{props.about}</a>
            <a href='/portfolio'>Portfolio</a>
          </div>
            
    
          <div className="header-right">
            <a href='https://www.linkedin.com/in/laura-coura/'>LinkedIn</a>
            <a href='https://github.com/LauraCoura'>GitHub</a>
    
            <button onClick={() => {setLanguage(1)}} className='button-image'>
              <picture className='language-image'>
                <source media="(min-height: 20px)" srcSet={brasil} className="language-image"/>
                <img src={brasil} className="language-image"/>
              </picture>
            </button>
    
            <button onClick={() => {setLanguage(0)}} className='button-image'>
              <picture className='language-image'>
                <source media="(min-height: 20px)" srcSet={usa} className="language-image"/>
                <img src={usa} className="language-image"/>
              </picture>
            </button>
          </div>
        </header>
      )
    }

    const MobileHeader = (props) => {
      <div className='mobile-header'>
        <div className='header-left'>
          {/* <button onClick={() => {setDisplayMenu(true)}} className='button-image'>
            <picture className='language-image'>
              <source media="(min-height: 20px)" srcset={menu} className="language-image"/>
              <img src={menu} className="language-image"/>
            </picture>
          </button> */}

          <Menu>
            <a href='/'>{props.home}</a>
            <a href='/#about'>{props.about}</a>
            <a href='/portfolio'>Portfolio</a>
            <a href='https://www.linkedin.com/in/laura-coura/'>LinkedIn</a>
            <a href='https://github.com/LauraCoura'>GitHub</a>
          </Menu>

        </div>

        <div className='header-right'>
          <button onClick={() => {setLanguage(1)}} className='button-image'>
            <picture className='language-image'>
              <source media="(min-height: 20px)" srcSet={brasil} className="language-image"/>
              <img src={brasil} className="language-image"/>
            </picture>
          </button>
    
          <button onClick={() => {setLanguage(0)}} className='button-image'>
            <picture className='language-image'>
              <source media="(min-height: 20px)" srcSet={usa} className="language-image"/>
              <img src={usa} className="language-image"/>
            </picture>
          </button>
        </div>
      </div>
    }

    const AboutMobile = (props) => {
      return(
        <div id='about'>
          <h1 className='h1-title'>{props.title}</h1>
    
          <article>
            {props.text1}
          </article>
    
          { (width <= 150) ?
            <div className='about-container-mobile-150'>
              <picture className='about-image-mobile'>
                <source media="(min-height: 70px)" srcSet={web} className="about-image-mobile"/>
                <img src={web} className="about-image-mobile"/>
              </picture>
      
              <article className='about-article-mobile'>
                Java, JavaScript, TypeScript, 
                C, C++, C#, Python, Haskell, 
                Kotlin, React Native, Android Studio,
                HTML, CSS, React
              </article>
            </div>
            :
            <div className='about-container-mobile'>
              <picture className='about-image-mobile'>
                <source media="(min-height: 70px)" srcSet={web} className="about-image-mobile"/>
                <img src={web} className="about-image-mobile"/>
              </picture>
      
              <article className='about-article-mobile'>
                Java, JavaScript, TypeScript,
                C, C++, C#, Python, Haskell,
                Kotlin, React Native, Android Studio,
                HTML, CSS, React
              </article>
            </div>
          }
    
          <div className='about-container-mobile'>
            <picture className='about-image-mobile'>
              <source media="(min-height: 70px)" srcSet={vector} className="about-image-mobile"/>
              <img src={vector} className="about-image-mobile"/>
            </picture>
    
            <article className='about-article-mobile'>
              Adobe Photoshop, Adobe XD, Adobe InDesign,
              Figma, Canva, Clip Studio Paint
            </article>
          </div>
    
          <div className='about-container-mobile'>
            <picture className='about-image-mobile'>
              <source media="(min-height: 70px)" srcSet={settings} className="about-image-mobile"/>
              <img src={settings} className="about-image-mobile"/>
            </picture>
    
              <article className='about-article-mobile'>
                Arduino, Git,
                Sony VEGAS Pro, Unity, Blender
              </article>
          </div>
    
          <div className='about-container-mobile'>
            <picture className='about-image-mobile'>
              <source media="(min-height: 70px)" srcSet={drawing} className="about-image-mobile"/>
              <img src={drawing} className="about-image-mobile"/>
            </picture>
    
            <article className='about-article-mobile'>
              {props.text2}, <br/>
              {props.text3}, <br/>
              {props.text4}
            </article>
    
          </div>
    
          <div className='about-container-mobile'>
            <picture className='about-image-mobile'>
              <source media="(min-height: 70px)" srcSet={language_image} className="about-image-mobile"/>
              <img src={language_image} className="about-image-mobile"/>
            </picture>
    
            <article className='about-article-mobile'>
              {props.text5} <br/>
              {props.text6} <br/>
              {props.text7} <br/>
              {props.text8} <br/>
            </article>
          </div>
        </div>
      )
    }

  return (
    <div className="app">
      {
        (width <= 1000) ? // MOBILE VERSION
          <div>
            {
              (language === 0 ) ?
                <MobileHeader home="Home" about="About"/>
              :
                <MobileHeader home="Início" about="Sobre"/>
            }

            {
              (language === 0) ? // - - - - - - - - - - - - - - - ENGLISH VERSION - - - - - - - - - - - - - - -
                <main className='main-mobile'>
                  <IntroductionMobile/>
                  
                  <picture className='main-image-mobile'>
                    <source media="(min-height: 350px)" srcSet={main} className="main-image-mobile"/>
                    <img src={main} className="main-image-mobile"/>
                  </picture>

                  <AboutMobile title="About me"
                    text1="With a degree in Computer Science and a technical degree in Industrial Automation, my career is focused on web and mobile (front-end) development. However, I also have interest in other fields of Computer Science, such as Artificial Intelligence, Data Mining and Machine Learning, and in the field of Graphic Design. Below, you can check out my qualifications and abilities:"
                    text2="Image and video editing"
                    text3="Illustration/digital art"
                    text4="UI/UX design"
                    text5="Portuguese: native"
                    text6="English: advanced"
                    text7="Spanish: average"
                    text8="Libras: basic" />
                  
                  <ContactMobile title="Contact" text="Want to talk to me? You can contact me through LinkedIn! But if you're more interested in checking out some of my codes, you can find it on my GitHub:"/>
                </main>
              :
                <main className='main-mobile'>
                  <IntroducaoMobile/>

                  <picture className='main-image-mobile'>
                    <source media="(min-height: 200px)" srcSet={main} className="main-image-mobile"/>
                    <img src={main} className="main-image-mobile"/>
                  </picture>

                  <AboutMobile title="Sobre mim"
                    text1="Formada em Ciência da Computação e técnica em Automação Industrial, minha carreira é voltada ao desenvolvimento (front-end) web e mobile. Entretanto, também tenho interesse em outras áreas de Computação, como Inteligência Artificial, Mineração de Dados e Machine Learning, e na área de Design Gráfico. Abaixo, você pode conferir as minhas competências e habilidades:"
                    text2="Edição de vídeos e imagens"
                    text3="Ilustração/arte digital"
                    text4="Design UI/UX"
                    text5="Português: língua materna"
                    text6="Inglês: avançado"
                    text7="Espanhol: intermediário"
                    text8="Libras: básico" />

                  <ContactMobile title="Contato" text="Quer falar comigo? Pode entrar em contato pelo LinkedIn! Mas se você estiver mais interessado em conferir alguns dos meus códigos, você pode encontrá-los no meu GitHub:"/>
                </main>
            }

            <footer>
              <picture className='bird-image'>
                <source media="(min-height: 70px)" srcSet={bird} className="bird-image"/>
                <img src={bird} className="bird-image"/>
              </picture>

              <p className='credits'>
                Icon credits: <a href="https://www.flaticon.com/free-icons/code" title="code icons">Icongeek26 </a> +
                <a href="https://www.flaticon.com/free-icons/graphic-design" title="graphic-design icons">xnimrodx </a> +
                <a href="https://www.flaticon.com/free-icons/settings" title="settings icons" >Flaticon</a> + 
                <a href="https://www.flaticon.com/free-icons/illustration" title="illustration icons">Darius Dan</a> +
                <a href="https://www.flaticon.com/free-icons/extensible-markup-language" title="extensible markup language icons">Culmbio</a>
              </p>
            </footer>
          </div>
        : // DESKTOP VERSION
        <div>
        {
          (language === 0) ?
            <Header home="Home" about="About"/>
          :
            <Header home="Início" about="Sobre"/>
        }

        {
          (language === 0) ? // - - - - - - - - - - - - - - - ENGLISH VERSION - - - - - - - - - - - - - - -
            <main className='main'>
              <Introduction/>
              <About title="About me"
                text1="With a degree in Computer Science and a technical degree in Industrial Automation, my career is focused on web and mobile (front-end) development. However, I also have interest in other fields of Computer Science, such as Artificial Intelligence, Data Mining and Machine Learning, and in the field of Graphic Design. Below, you can check out my qualifications and abilities:"
                text2="Image and video editing"
                text3="Illustration/digital art"
                text4="UI/UX design"
                text5="Portuguese: native"
                text6="English: advanced"
                text7="Spanish: average"
                text8="Libras: basic" />
              <Contact title="Contact" text="Want to talk to me? You can contact me through LinkedIn! But if you're more interested in checking out some of my codes, you can find it on my GitHub:"/>
            </main>
              : // - - - - - - - - - - - - - - - PORTUGUESE VERSION - - - - - - - - - - - - - - -
              <main className='main'> 
                <Introducao/>
                <About title="Sobre mim"
                  text1="Formada em Ciência da Computação e técnica em Automação Industrial, minha carreira é voltada ao desenvolvimento (front-end) web e mobile. Entretanto, também tenho interesse em outras áreas de Computação, como Inteligência Artificial, Mineração de Dados e Machine Learning, e na área de Design Gráfico. Abaixo, você pode conferir as minhas competências e habilidades:"
                  text2="Edição de vídeos e imagens"
                  text3="Ilustração/arte digital"
                  text4="Design UI/UX"
                  text5="Português: língua materna"
                  text6="Inglês: avançado"
                  text7="Espanhol: intermediário"
                  text8="Libras: básico" />
                <Contact title="Contato" text="Quer falar comigo? Pode entrar em contato pelo LinkedIn! Mas se você estiver mais interessado em conferir alguns dos meus códigos, você pode encontrá-los no meu GitHub:"/>
              </main>
          }

          <footer>
            <picture className='bird-image'>
              <source media="(min-height: 70px)" srcSet={bird} className="bird-image"/>
              <img src={bird} className="bird-image"/>
            </picture>

            <p className='credits'>
              Icon credits: <a href="https://www.flaticon.com/free-icons/code" title="code icons">Icongeek26 </a> +
              <a href="https://www.flaticon.com/free-icons/graphic-design" title="graphic-design icons">xnimrodx </a> +
              <a href="https://www.flaticon.com/free-icons/settings" title="settings icons" >Flaticon</a> + 
              <a href="https://www.flaticon.com/free-icons/illustration" title="illustration icons">Darius Dan</a> +
              <a href="https://www.flaticon.com/free-icons/extensible-markup-language" title="extensible markup language icons">Culmbio</a>
            </p>
          </footer>
        </div>
      }
    </div>
  );
}

export default Home;